:root {
  --colorBg: #e5dfd3;
  --colorText: #2b2b2b;
  --colorAccent: #316745;
}

:root.is-darkmode {
  --colorBg: #383735;
  --colorText: #e5dfd3;
}

body {
  font-size: 1rem;
  background-color: var(--colorBg);
  color: var(--colorText);
  font-family: "Shippori Mincho",serif;
  font-weight: 500;
}

body.ua-macos {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.ua-chrome img,
body.ua-chrome source {
  image-rendering: -webkit-optimize-contrast;
}

.pin-spacer {
  pointer-events: none;
}

.l-drawer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 300;
  width: 100%;
  height: 100vh;
}

.l-drawer__inner {
  display: flex;
  justify-content: center;
  position: relative;
  flex-direction: column;
  background-color: var(--colorText);
  width: 100%;
  height: 100vh;
  padding-left: 5.3333333333vw;
  overflow: hidden;
}

.l-drawer__items {
  margin-left: 2.6666666667vw;
}

.l-drawer__item {
  position: relative;
  font-size: min(10.6666666667vw,72px);
  text-transform: uppercase;
  letter-spacing: .05em;
  line-height: 1;
  overflow: hidden;
}

.l-drawer__item+.l-drawer__item {
  margin-top: 1em;
}

.l-drawer__link {
  display: block;
  transition: transform .2s cubic-bezier(0, 0.55, 0.45, 1);
  color: var(--colorBg) !important;
  font-family: "Roboto Mono",monospace;
  white-space: nowrap;
}

.l-drawer__link:active {
  transform: translate3d(0, 0, 0) scale(0.9) !important;
}

.l-drawer__close.c-hamburger--close {
  position: relative;
  left: auto;
  right: auto;
  margin-top: 10vh;
  align-self: flex-start;
  overflow: hidden;
}

.l-drawer[aria-expanded=false] {
  visibility: hidden;
  opacity: 0;
}

.l-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  font-family: "Roboto Mono",monospace;
}

.l-footer__inner {
  max-height: 80px;
  padding: 5.3333333333vw 6.6666666667vw;
  display: flex;
  align-items: center;
}

.l-footer__items {
  position: relative;
  display: flex;
  align-items: center;
}

.l-footer__link {
  width: min(5.3333333333vw,40px);
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.l-header__inner {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 2.6666666667vw 5.3333333333vw 2.6666666667vw 4vw;
  font-family: "Roboto Mono",monospace;
}

.l-header__inner:not(:first-of-type) {
  display: none;
}

.l-header__inner--right {
  top: 0;
  right: 0;
  width: 80px;
  height: 100vh;
}

.l-header__inner--bottom {
  bottom: 0;
  left: 0;
  height: 80px;
}

.l-header__inner--left {
  top: 0;
  left: 0;
  width: 80px;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.l-header__logo {
  display: inline-block;
  width: 10.6666666667vw;
}

.l-header__rowTitle {
  font-size: 20px;
  font-family: "Roboto Mono",monospace;
  letter-spacing: .1em;
  writing-mode: vertical-rl;
  color: var(--colorBg);
}

.l-header__twitter {
  width: 40px;
}

.l-header__twitter .st0 {
  fill: var(--colorBg) !important;
}

.l-header__nav {
  display: none;
}

.l-header__menus {
  display: flex;
  align-items: center;
}

.l-header__menu {
  position: relative;
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: .05em;
  overflow: hidden;
}

.l-header__menu+.l-header__menu {
  margin-left: 1em;
}

.l-header__menu:last-of-type svg {
  margin-left: .2em;
}

.l-header__link {
  position: relative;
  display: block;
  text-decoration: none;
  overflow: hidden;
  color: var(--colorBg) !important;
}

.l-header__link span {
  display: inline-block;
  transform-origin: center center .5em;
}

.l-header__link span:nth-of-type(2) {
  -webkit-animation-delay: .04s;
  animation-delay: .04s;
}

.l-header__link span:nth-of-type(3) {
  -webkit-animation-delay: .08s;
  animation-delay: .08s;
}

.l-header__link span:nth-of-type(4) {
  -webkit-animation-delay: .12s;
  animation-delay: .12s;
}

.l-header__link span:nth-of-type(5) {
  -webkit-animation-delay: .16s;
  animation-delay: .16s;
}

.l-header__link span:nth-of-type(6) {
  -webkit-animation-delay: .2s;
  animation-delay: .2s;
}

.l-header__link span:nth-of-type(7) {
  -webkit-animation-delay: .24s;
  animation-delay: .24s;
}

.l-loading {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh)*100);
  content: "";
  background-color: #e5dfd3;
  pointer-events: none;
}

.l-loading.is-done {
  -webkit-animation: spiral .8s ease-out .2s 1 forwards;
  animation: spiral .8s ease-out .2s 1 forwards;
