.l-loading__logo {
  display: block;
  position: absolute;
  width: 26.6666666667vw;
  max-width: 200px;
  transform: scale(0);
  opacity: 0;
}

.l-loading__text {
  position: absolute;
  font-size: 7.4666666667vw;
  font-family: "Roboto Mono",monospace;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: 1;
  color: #2b2b2b;
}

.l-loading__text span {
  opacity: 0;
}

.l-loading__text span:nth-child(1) {
  -webkit-animation: blink1 1.2s linear infinite;
  animation: blink1 1.2s linear infinite;
}

@-webkit-keyframes blink1 {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blink1 {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.l-loading__text span:nth-child(2) {
  -webkit-animation: blink2 1.2s linear infinite;
  animation: blink2 1.2s linear infinite;
}

@-webkit-keyframes blink2 {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blink2 {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.l-loading__text span:nth-child(3) {
  -webkit-animation: blink3 1.2s linear infinite;
  animation: blink3 1.2s linear infinite;
}

@-webkit-keyframes blink3 {
  0% {
    opacity: 0;
  }

  70% {
    opacity: 0;
  }

  80% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes blink3 {
  0% {
    opacity: 0;
  }

  70% {
    opacity: 0;
  }

  80% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.l-loading__face {
  position: absolute;
  width: 40vw;
  height: 40vw;
  background-color: #e5dfd3;
  transform: scale(0);
  mix-blend-mode: overlay;
}

.l-loading__face:nth-of-type(1) {
  z-index: 3;
}

.l-loading__face:nth-of-type(2) {
  z-index: 2;
}

.l-loading__face:nth-of-type(3) {
  z-index: 1;
}

.l-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh)*100);
}

.l-main {
  flex: 1;
  background-color: var(--colorBg);
  color: var(--colorText);
  width: 100%;
  padding: 0 5.3333333333vw;
}

.l-main__inner {
  width: 100%;
  height: 100%;
  padding: 21.3333333333vw 0;
  overflow-y: scroll;
}

.l-main__title {
  font-size: min(16vw,120px);
  font-weight: 700;
  letter-spacing: .05em;
  line-height: 1;
  text-transform: uppercase;
}

:root.is-darkmode .c-colorIcon {
  fill: var(--colorText);
}

.c-externalLink {
  display: inline-block;
  color: var(--colorText);
  line-height: 1.2;
}

.c-externalLink svg {
  align-self: center;
  width: .9em;
  height: auto;
}

.c-externalLink__icon {
  display: inline-block;
  position: relative;
  width: .5em;
  height: .4em;
  margin: 0 .3em;
}

.c-externalLink__icon::before,
.c-externalLink__icon::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  border-style: solid;
  border-color: var(--colorText);
}

.c-externalLink__icon::before {
  top: -0.2em;
  right: 0;
  border-width: 1px;
}

