@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    transition-duration: .01ms !important;
    -webkit-animation-duration: .01ms !important;
    animation-duration: .01ms !important;
  }
}

@media (hover: hover) {
  .l-header__link:hover span {
    -webkit-animation-name: rotating;
    animation-name: rotating;
    -webkit-animation-duration: .4s;
    animation-duration: .4s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

@-webkit-keyframes rotating {
    0% {
      transform: rotateX(0deg);
    }

    25% {
      transform: rotateX(90deg);
    }

    25.1% {
      transform: rotateX(91deg);
      opacity: 0;
    }

    49.9% {
      transform: rotateX(269deg);
      opacity: 0;
    }

    50% {
      transform: rotateX(270deg);
      opacity: 1;
    }

    75%, 100% {
      transform: rotateX(360deg);
      opacity: 1;
    }
}

@keyframes rotating {
    0% {
      transform: rotateX(0deg);
    }

    25% {
      transform: rotateX(90deg);
    }

    25.1% {
      transform: rotateX(91deg);
      opacity: 0;
    }

    49.9% {
      transform: rotateX(269deg);
      opacity: 0;
    }

    50% {
      transform: rotateX(270deg);
      opacity: 1;
    }

    75%, 100% {
      transform: rotateX(360deg);
      opacity: 1;
    }
}

  .c-readmore:hover {
    opacity: .5;
  }

  .p-works-slider__button:hover {
    transform: translateY(-50%) scale(1.2);
  }

  .p-works-slider__button:hover::before {
    border: 2px dotted var(--colorText);
    -webkit-animation: rotateAnime 10s linear infinite;
    animation: rotateAnime 10s linear infinite;
  }

@-webkit-keyframes rotateAnime {
    0% {
      transform: translateY(-50%) rotate(0);
    }

    100% {
      transform: translateY(-50%) rotate(360deg);
    }
}

@keyframes rotateAnime {
    0% {
      transform: translateY(-50%) rotate(0);
    }

    100% {
      transform: translateY(-50%) rotate(360deg);
    }
}

  .p-works-slider__button--prev:hover::before {
    -webkit-animation: rotateAnimeRev 10s linear infinite;
    animation: rotateAnimeRev 10s linear infinite;
  }

@-webkit-keyframes rotateAnimeRev {
    0% {
      transform: translateY(-50%) rotate(0);
    }

    100% {
      transform: translateY(-50%) rotate(-360deg);
    }
}

@keyframes rotateAnimeRev {
    0% {
      transform: translateY(-50%) rotate(0);
    }

    100% {
      transform: translateY(-50%) rotate(-360deg);
    }
}

